import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'
import PortfolioView from '../views/PortfolioView.vue'
import ContactView from '../views/ContactView.vue'
import TraditionalPainting from '../views/TraditionalPainting.vue'
import DataVisualizationView from '../views/DataVisualizationView.vue'
import IllustrationView from '../views/IllustrationView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/portfolio',
    name: 'portfolio',
    component: PortfolioView
  },
  {
    path: '/contact',
    name: 'contact',
    component: ContactView
  },
  {
    path: '/traditionalpainting',
    name: 'traditionalpainting',
    component: TraditionalPainting
  },
  {
    path: '/datavisualization',
    name: 'datavisualization',
    component: DataVisualizationView,
  },
  {
    path: '/digitalillustration',
    name: 'digitalillustration',
    component: IllustrationView,
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
